<template>
    <div>
         <table class="table mt-4" v-if="sortOrder.length">
      <thead>
        <tr>
          <th>購買時間</th>
          <th>Email</th>
          <th>購買款項</th>
          <th class="text-right">應付金額</th>
          <th>是否付款</th>
          <th>修改訂單</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in sortOrder"    :key="key"     :class="{'text-secondary': !item.is_paid}" >
          <td>{{ item.create_at | date }}</td>
          <td><span v-text="item.user.email" v-if="item.user"></span></td>
          <td>
            <ul class="list-unstyled">
              <li v-for="(product, i) in item.products" :key="i">
                {{ product.product.title }} 數量：{{ product.qty }}
                {{ product.product.unit }}
              </li>
            </ul>
          </td>
          <td class="text-right">{{ item.total | currency }}</td>
          <td>
            <strong v-if="item.is_paid" class="text-success">已付款</strong>
            <span v-else class="text-muted">尚未起用</span>
          </td>
          <td>
               <button class="btn btn-outline-danger btn-sm" @click="modifyOrder(item)">編輯</button>
          </td>
        </tr>
      </tbody>
    </table>
    <CustomerOrderModal :sortOrderitem='sortOrderitem' :sortOrderproducts="sortOrderproducts" :products="products"></CustomerOrderModal>
    </div>
</template>

<script>
import $ from "jquery";
import CustomerOrderModal from '../CustomerOrderModal';
export default {
    data:function(){
        return{
            sortOrder:[],
            sortOrderitem:{},
            sortOrderproducts:[],
            products:[],
        }
    },
    methods:{
        getOrder(page = 1){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/orders?page${page}`;
            this.$http.get(url).then((response) => {
            console.log('getOrder',response.data.orders);
            vm.sortOrder = response.data.orders
      });
        },
        modifyOrder(item){
            $('#orderModal').modal('show');     //透過vue的methods去觸發新增產品，避免資料尚未載入就用bootstrap的jquery觸發
            this.sortOrderitem = item;
            this.sortOrderproducts = Object.keys(this.sortOrderitem.products)
            this.products = this.sortOrderitem.products;

        }
    },
    created(){
        this.getOrder()
    },
    components:{
        CustomerOrderModal
    }
}
</script>


