<template>
         <div class="col-lg-12">
         <div class="form-inline form-inline-chang">
         <div class="input-group mb-3">
        <input type="text" class="form-control" placeholder="Search" aria-label="Recipient's username" aria-describedby="basic-addon2" v-model="Search" >
        <div class="input-group-append">
        <button class="btn btn-danger text-white" type="button" id="button-addon2" @click.prevent="searchProduct">Search</button>
        </div>
        </div>   
        </div>
        </div>
</template>

<script>
import bus from '../eventBus'
export default {
    data(){
        return{
            filterProducts:{},
            productsAll:[],
            Search:""
        }
    },
    methods:{
        searchProduct(){
              const vm = this;
              vm.filterProducts =  this.productsAll.filter(function(item){
                    return item.title == vm.Search
                })
                bus.$emit('filterProducts',vm.filterProducts)
        }
    },
    created(){
        bus.$on('productsAll', (item) => {
            this.productsAll = item
     })
        bus.$on('filter', (item) => {
            this.filterProducts = item
     })
    }
}
</script>


