<template>
    <div>
                <loading :active.sync="isLoading"></loading>     <!-- loading元件 -->

                <div class="row justify-content-center mt-5">
                <div class="col-lg-8">
                        <div class="tab-content">
                        <div class="tab-pane active" id="payment" role="tabpanel">
                        <div class="accordion" id="accordionExample">
                                <div class="card">
                                <div class="card-header badge-danger d-flex justify-content-end align-items-end " id="headingOne">
                                    <button class="btn btn-link color-white-a " type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                            顯示購物車細節
                                    </button>
                                    <span class="ml-auto h2 mb-0" v-if="cart.final_total>0">{{cart.final_total | currency}}</span>
                                </div>
                                </div>
                                <div id="collapseOne" class="collapse " aria-labelledby="headingOne" data-parent="#accordionExample">
                                  <table class="table">
                                        <tbody>
                                                <tr v-for="item in cart.carts" :key='item.id'>
                                                <th scope="row" >
                                                <a href="#" class='text-danger'><i class="fas fa-trash-alt" @click.prevent="removeCartItem(item.id)"></i></a>
                                                </th>
                                                <td><img :src=item.product.imageUrl style="height:30px;"></td>
                                                <td >{{item.product.title}}</td>
                                                <td>{{item.qty}}{{item.product.unit}}</td>
                                                <td class="text-right">{{item.product.price | currency}}</td>
                                                </tr>
                                        </tbody>
                                        <tfoot v-if="cart.total > 0 ">
                                         <tr>
                                        <td></td>
                                        <td colspan="3" class="text-right">總計</td>
                                        <td class="text-right">{{ cart.total | currency}}</td>
                                        </tr>
                                        <tr v-if="cart.final_total !== cart.total">
                                        <td></td>
                                        <td colspan="3" class="text-right text-success">折扣價</td>
                                        <td class="text-right text-success">{{ cart.final_total| currency }}</td>
                                       </tr>
                                        </tfoot>
                                        </table>           
                                </div>

                            </div>
                                 <div class="input-group mb-3 input-group-sm mt-5" v-if="cart.total > 0">
                                <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
                                <div class="input-group-append">
                                 <button class="btn btn-danger text-white" type="button" @click="addCouponCode">套用優惠碼 </button>
                                 </div>
                                 </div>

                            
                            <form  class="needs-validation" novalidate @submit.prevent='createOrder'>  
                                    <div class="form-row mt-5">
                                            <div class="col-md-12 py-3 bg-danger text-center text-white mb-2">訂購人資訊</div>
                                            <div class="form-group col-md-6 ">
                                                    <label for="name">姓名</label>
                                                    <input type="text" class="form-control" id="name" placeholder="請輸入收件人姓名" data-vv-name="name"   v-model="form.user.name" v-validate="'required'" :class="{'is-invalid':errors.has('name')}">
                                                    <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
                                                  </div>
                                      <div class="form-group col-md-6">
                                        <label for="Email">Email</label>
                                        <input type="email" class="form-control" id="Email" placeholder="Email" data-vv-name="email" v-model="form.user.email" v-validate="'required|email'" :class="{'is-invalid':errors.has('email')}">
                                        <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
                                      </div>
                                    </div>
                                     <div class="form-group">
                                      <label for="phone">電話</label>
                                      <input type="tel" class="form-control" id="phone" placeholder="請輸入電話" data-vv-name="phone" v-validate="'required|mobile'" v-model="form.user.tel" :class="{'is-invalid':errors.has('phone')}">
                                      <span class="text-danger" v-if="errors.has('phone')" >{{ errors.first('phone') }}</span>
                                    </div>
                                    <div class="form-group">
                                      <label for="inputAddress">地址</label>
                                      <input type="text" class="form-control" id="inputAddress" placeholder="請輸入地址" required>
                                    </div>
                                    <div class="form-group">
                                      <label for="message">備註</label>
                                      <textarea name="" id="message" class="form-control" cols="30" rows="5" ></textarea>
                                    </div>

                                    <div class="text-right">
                                            <button type="submit" class="btn btn-secondary mr-2" @click="goHome">繼續選購</button>
                                            <button type="submit" class="btn btn-danger">確認付款</button>
                                    </div>
                                    
                                  </form>
                                  </div>

                                
                        </div>
                        
                        
                </div>
                
            </div>
    </div>
</template>

<script>
import VeeValidate, {Validator} from 'vee-validate';
import zhTW from 'vee-validate/dist/locale/zh_TW';
export default {
    data(){
        return{
            cart:{},
            coupon_code:"",
            isLoading:false,
              form:{
                user:{
                    name:"",
                    email:"",
                    tel:"",
                    address:"",
                },
                message:""
            },
        }
    },
    methods: {
        getCarts(){
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/cart`;
        this.$http.get(url).then((response) => {
        console.log('getCart',response.data);
        vm.cart = response.data.data
      });
        },
        addCouponCode(){
        const vm = this;
    vm.isLoading =true;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/coupon`;
      const coupon ={
          code:vm.coupon_code
      }
      this.$http.post(url,{data:coupon}).then((response) => {
        if(response.data.success){
          vm.getCarts()
          this.$bus.$emit('message:push','已套用優惠卷','danger') //上傳成功Alert訊息 
          vm.isLoading = false;
          console.log('addCouponCode',response);
        }else{
          this.$bus.$emit('message:push','無此優惠卷','danger') //上傳成功Alert訊息
          vm.isLoading = false;
          console.log('無此優惠卷')
        }
       
        })
        },
        removeCartItem(id){
            const vm = this;
      vm.isLoading = true;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/cart/${id}`;
      this.$http.delete(url).then((response) => {
        console.log('removeCartItem',response);
       vm.getCarts() 
        vm.isLoading = false;
        })
        },
        createOrder(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/order`;
      const order = vm.form
      this.$validator.validate().then(valid => {
        
        if (valid) {
          vm.isLoading =true;
        this.$http.post(url,{data:order}).then((response) => {
        console.log('訂單已建立',response);
        if(response.data.success){
          vm.$router.push(`/Checkouts/Checkoutpage/${response.data.orderId}`)
          vm.isLoading = false;
          this.$emit('changeOrder'); //內部元件觸發
        }else{
          console.log('欄位不完整')
           vm.isLoading = false;
        }
        })
        }
        vm.isLoading = false;
      });
        },
        goHome(){
            this.$router.push('/Home/Menu')
        }
    },
    created(){
        this.getCarts()
        this.validator = new Validator({});

      Validator.extend('mobile', {
        getMessage: field => "請輸入正確的手機號碼",
        validate: value =>  /^09[0-9]{8}$/.test(value)
      });
      
      this.validator.attach({name: 'phone', rules: 'required|mobile', alias: '手機'});
    },
  
}
</script>


<style >
.alert-rounded{
    border-radius: 50px;
}
.color-white-a{
    color: #fff !important;
    text-decoration: none !important;
}
</style>


