<template>
    <div class="col-lg-12">
             <nav aria-label="Page navigation example" v-if="filterProducts.length >9">
              <ul class="pagination justify-content-center ">
             <li class="page-item " :class="{'disabled':!pagination.has_pre}">
              <a class="page-link  bg-hover color-danger" href="#" aria-label="Previous"  @click.prevent='getProducts(pagination.current_page - 1)'>
             <span aria-hidden="true">&laquo;</span>
              </a>
             </li>
             <li class="page-item" v-for="page in pagination.total_pages" :key='page' :class="{'active':pagination.current_page === page}" @click.prevent='getProducts(page)'>
               <a class="page-link " href="#"  >{{ page }}</a></li>

               <li class="page-item" :class="{'disabled':!pagination.has_next}">
                <a class="page-link  bg-hover color-danger" href="#" aria-label="Next" @click.prevent='getProducts(pagination.current_page + 1)'>
                 <span aria-hidden="true">&raquo;</span>
                 </a>
                 </li>
                 </ul>
                  </nav>
    </div>
</template>

<script>
import bus from '../eventBus'
export default {
    data(){
        return{
            pagination:{},
            filterProducts:{}
        }
    },
    methods: {
        getProducts(page){                         //將pagination.current_page 目前頁數代入
            //this.$emit('getPageProducts',page)     //內部元件觸發，將外層props內容emit回去
            bus.$emit('getPageProducts',page)
            console.log(page)
        }
    },
    created(){
        bus.$on('pagination', (item) => {
            this.pagination = item
     })
     bus.$on('filter', (item) => {
            this.filterProducts = item
     })
    }
}
</script>



