<template>
    <div>
        <div class="modal fade" id="removeVoucherModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header bg-danger text-white">
        <h5 class="modal-title" id="exampleModalLabel">刪除優惠卷</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        是否刪除 <strong class="text-danger">{{ this.data.title }}</strong> 優惠卷(刪除後將無法恢復)。
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
        <button type="button" class="btn btn-primary" @click="removeVoucher">刪除</button>
      </div>
    </div>
  </div>
</div>
    </div>
</template>

<script>
import $ from "jquery";
export default {
    props:['data'],
    methods:{
        removeVoucher(){
        const api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/coupon/${this.data.id}`;      
        const vm =this;
        this.$http.delete(api).then((response) => {
        console.log('removeVoucher',response.data)
        $('#removeVoucherModal').modal('hide');                      //開啟刪除model
        this.$emit('removeVoucher')

        })
        }
    }
}
</script>


