<template>
    <div>
        <Alert/> 
        <div class="container py-5">
        <h1 class="text-center mb-3 text-secondary font-weight-bold">六角血拚 結帳</h1>
        <div class="form-row text-center">
                <div class="col-12 col-sm ">
                    <div   :class="{'badge-danger':badge,'text-danger':text}" role="alert" class="alert alert-rounded border-danger">    
                            輸入訂單資料
                    </div>
                </div>
                <div class="col-12 col-sm">
                <div class="alert  alert-rounded border border-danger " role="alert" :class="{'badge-danger':order,'text-white':order,'alert-light':!order,'text-danger':!order}">
                            確認訂單資訊
                </div>
                </div>
                <div class="col-12 col-sm">
                        <div class="alert alert-rounded border border-danger" role="alert" :class="{'badge-danger':!chech,'text-white':!chech,'alert-light':chech,'text-danger':chech}">
                                付款完成
                        </div>
                </div>
         </div> <!-- form-row -->
            <router-view  @changeOrder="Order" @changeCheck='changeCheck'></router-view>
        </div>    <!-- container -->
        
    </div>
</template>


<script>
import Alert from './AlertMessage';  //載入Aleart元件
export default {
    data(){
        return{
            badge:true,
            text:false,
            order:false,
            chech:true,
        }
    },
    methods:{
        Order(){
            this.badge = false
            this.text  = true
            this.order = true
        },
        changeCheck(){
            this.order = false
            this.chech = false
        }
    },
    components:{
        Alert
    }

}
</script>

<style>
.container{
    font-family: "微軟正黑體"
}

</style>



