<template>

        <div class="dropdown ml-auto">
            <loading :active.sync="isLoading"></loading>     <!-- loading元件 -->
        <a class="btn btn-outline-danger  btn-sm btn-cart" href="#"  data-toggle="dropdown" id="ShoppingCart" aria-haspopup="true" aria-expanded="false">
        <i class="fas fa-shopping-cart fa-2x text-danger"></i>
        <span class="badge badge-pill badge-danger border border-light" v-if="number>0">{{number}}</span>
      </a>
      <div class="dropdown-menu dropdown-menu-right" style='min-width:395px'>
                        <div class="px-2 py-1">
                                <h6 class='my-0 mb-3' >已選擇商品</h6>
                                        <table class="table">
                                        <tbody>
                                                <tr v-for="item in cart.carts" :key='item.id'>
                                                <th scope="row" >
                                                <a href="#" class='text-danger'><i class="fas fa-trash-alt" @click.prevent="removeCartItem(item.id)"></i></a>
                                                </th>
                                                <td><img :src=item.product.imageUrl style="height:30px;"></td>
                                                <td >{{item.product.title}}</td>
                                                <td>{{item.qty}}{{item.product.unit}}</td>
                                                <td class="text-right">{{item.product.price | currency}}</td>
                                                </tr>
                                        </tbody>
                                        <tfoot v-if="cart.total > 0 ">
                                         <tr>
                                        <td></td>
                                        <td colspan="3" class="text-right">總計</td>
                                        <td class="text-right">{{ cart.total | currency}}</td>
                                        </tr>
                                        <tr v-if="cart.final_total !== cart.total">
                                        <td></td>
                                        <td colspan="3" class="text-right text-success">折扣價</td>
                                        <td class="text-right text-success">{{ cart.final_total | currency}}</td>
                                       </tr>
                                        </tfoot>
                                        </table>      
                                        <a href="#" class="btn btn-danger btn-block text-white" v-if="cart.total>0" @click.prevent="checkOut">結帳</a> 
                        </div>
                        </div>
                </div>
  
</template>


<script>
import bus from '../eventBus'
import $ from 'jquery';
export default {
    
    data(){
        return{
            cart:{},
            isLoading:false,
            number:"",
            box:'',
        }
    },
    methods:{
        getShoppingCart(){
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/cart`;
        this.$http.get(url).then((response) => {
        console.log('getCart',response.data);
        vm.cart = response.data.data
        vm.number = vm.cart.carts.length
      });
        },
    removeCartItem(id){
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/cart/${id}`;
      this.$http.delete(url).then((response) => {
        console.log('removeCartItem',response);
       vm.getShoppingCart()
       vm.$bus.$emit('message:push','已刪除商品','danger') //上傳成功Alert訊息 
        vm.isLoading = false;
        })
        },
        checkOut(){
        this.$router.push('/Checkouts/ChechOutpage')
        }
    },
    created(){
        const vm = this
        bus.$on('cart', (shoppingCart) => {
            this.cart = shoppingCart;
            this.number = shoppingCart.carts.length
     })
    bus.$on('carts', (item) => {
            vm.getShoppingCart()
     })
     bus.$on('Update', (item) => {
           vm.getShoppingCart()
     })
    }
}
</script>

<style lang="">
.btn-cart .badge{
    position: absolute;
    top: -8px;
    right: -15px;
}

.btn-cart{
    background-color: transparent;
    position: relative;
}  
</style>


