<template>
    <div>
    <div class="text-right mt-4">
      <button class="btn btn-dark" @click="addVoucher">建立新的優惠卷</button>      <!-- openModal(true) = isNew = true -->
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>名稱</th>
          <th>折扣百分比</th>
          <th>到期日</th>
          <th>是否啟用</th>
          <th>編輯</th>
          <th>刪除</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ item.due_date | date }}</td>
          <td>
            <span v-if="item.is_enabled === 1" class="text-success">啟用</span>
            <span v-else class="text-muted">未起用</span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm"
              @click="addVoucher(false, item)"
            >編輯</button>
          </td>
          <td>
            <button class="btn btn-outline-danger btn-sm" @click="removeVoucher(item)"
            >刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
     <VoucherModal :data='data' :isNew='isNew' @addVoucher='getcoupons'></VoucherModal>
     <RemoveVoucherModal :data='data' @removeVoucher='getcoupons'></RemoveVoucherModal>
    </div>
</template>


<script>
import $ from "jquery";
import VoucherModal from "../VoucherModal"
import RemoveVoucherModal from "../RemoveVoucherModal"
export default {
    data(){
        return{
            coupons:[],
             data:{
                title:"",
                is_enabled:Boolean,
                percent:"",
                due_date:0,
                code:"",
            },
            isLoading:false,
            isNew:Boolean,
        }
        
    },
    methods:{
        addVoucher(isNew,item){
        const vm = this;
        if(isNew){
            this.data ={};
            this.isNew = true;
        }else{
            this.data = Object.assign({},item);
            this.isNew = false;
        }
         $('#VoucherModal').modal('show');         //將modal打開 
        },
        getcoupons(page = 1){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/coupons?page=${page}`;
      this.$http.get(url).then((response) => {
      console.log('getcoupons',response);
      vm.coupons = response.data.coupons
    });
        },
        removeVoucher(item){
            this.data = item
            $('#removeVoucherModal').modal('show');         //將modal打開 
        }
    },
    created(){
        this.getcoupons()
    },
    components:{
        VoucherModal,
        RemoveVoucherModal,
    }
}
</script>


