import Vue from 'vue';
import VueRouter from 'vue-router';
import dashboard from '@/components/Dashboard';
import Login from '@/components/pages/Login';
import Products from '@/components/pages/Products';
import Order from '@/components/pages/Order';
import Voucher from '@/components/pages/Voucher';
import Home from '@/components/Home';
import ProductsPage from '@/components/ProductsPage';
import Checkouts from '@/components/Checkouts';
import ChechOutpage from '@/components/pages/ChechOutpage';
import CustomerCheckoutpage from '@/components/pages/CustomerCheckoutpage';
import CarryOutOrder from '@/components/pages/CarryOutOrder';
Vue.use(VueRouter);                          //啟用VueRouter

export default new VueRouter({
    routes:[
        {
            path:'*',                     //使用者輸入非設定虛擬路徑會傳跳成/login
            redirect:'/Home'             //設定路由預設首頁
        },
        {
            name:'Login',
            path:'/login',
            component:Login,
        },
        {
          name:'Home',
          path:'/Home',
          component:Home,
      },
      {                            
        name:'ProductsPage',
        path:'/ProductsPage/:id',
        component:ProductsPage,
      },
      {
        name:'Checkouts',
        path:'/Checkouts',
        component:Checkouts,
        children:[
          {
            name:'ChechOutpage',
            path:'ChechOutpage',
            component:ChechOutpage,
          },
          {
            name:'CustomerCheckoutpage',
            path:'Checkoutpage/:orderId',
            component:CustomerCheckoutpage,
          },
          {
            name:'CarryOutOrder',
            path:'CarryOutOrder',
            component:CarryOutOrder,
          },
          
        ]
    },
          {
            name:'dashboard',
            path:'/admin',
            component:dashboard,
            children:[
                {                              //admin底下的子路由
                   name:'products',
                   path:'Products',
                   component:Products,
                   meta:{requiresAuth:true},  //設置進入此網站必須為true
                },
                  {                              //admin底下的子路由
                    name:'Order',
                    path:'Order',
                    component:Order,
                    meta:{requiresAuth:true},  //設置進入此網站必須為true
                 },
                  {                              //admin底下的子路由
                    name:'Voucher',
                    path:'Voucher',
                    component:Voucher,
                    meta:{requiresAuth:true},  //設置進入此網站必須為true
                 },
               
            ],
        },
      //   {
      //     name:'dashboardCustomer',
      //     path:'/',
      //     component:dashboard,
      //     children:[
      //          {                              //admin底下的子路由
      //           name:'CustomerOrders',
      //           path:'CustomerOrders',
      //           component:CustomerOrders,
      //        },
      //        {                              //admin底下的子路由
      //         name:'ShoppingCart',
      //         path:'ShoppingCart',
      //         component:ShoppingCart,
      //      },
      //      {                              //admin底下的子路由
      //       name:'ChechOut',
      //       path:'ChechOut',
      //       component:ChechOut,
      //    },
      //    {                              //admin底下的子路由
      //     name:'CustomerCheckout',
      //     path:'CustomerCheckout/:orderId',
      //     component:CustomerCheckout,
      //  },
             
      //     ],
      // },
    ]
})


