<template>
    <div>
      <Alert/>
      <HomeNavbar/>
      <Jumbotron/>
           <loading :active.sync="isLoading"></loading>     <!-- loading元件 -->
        <div class="container">
                                    <nav aria-label="breadcrumb "> <!-- 麵包屑 -->
                                        <ol class="breadcrumb bg-white pl-0 ">
                                        <li class="breadcrumb-item "><a href="#" class="color-danger-a" @click.prevent="home">首頁</a></li>
                                        <li class="breadcrumb-item"><a href="#t-shirts" class="color-danger-a" @click.prevent="List">{{product.category}}t</a></li>
                                        <li class="breadcrumb-item active" aria-current="page">{{product.title}}</li>
                                        </ol>
                        </nav>
                         
                <div class="row mt-4">
                        <div class="col-lg-4">
                          <div class="sticky-top" style="top: 30px;">
                                        <h1 class="h2 font-weight-bold">{{product.title}}</h1>     
                                        <div class="d-flex justify-content-end align-items-end">
                                              <p class='text-secondary mb-0'><del>售價{{product.origin_price | currency}}</del></p>
                                              <div class="h3 ml-auto mb-0 text-danger">
                                                <small>特價 NT$</small>
                                                <strong>{{product.price | currency}}</strong>      
                                              </div> 
                                        </div>
                                        <hr>
                                        <p>尺寸:</p>
                                        
                                        <div class="btn-group mb-3" role="group" aria-label="Basic example">
                                                      <button type="button" class="btn btn-danger text-white">S</button>
                                                      <button type="button" class="btn btn-danger text-white">M</button>
                                                      <button type="button" class="btn btn-danger text-white" disabled>L</button>
                                                      <button type="button" class="btn btn-danger text-white">XL</button>
                                                    </div>
                                                    <div class="input-group my-2 ">
                                                              <select name="" id="" class="form-control mr-4 rounded" v-model="num">
                                                                         <option :value="num" v-for="num in 10" :key="num">
                                                                                選購 {{num}} {{product.unit}}
                                                                         </option>
                                                              </select>
                                                            
                                                               <a href="#" class="btn btn-danger"  @click.prevent="addCart"><i class="fas fa-shopping-cart text-white mr-2"></i>加入購物車</a>
                                                                 <!-- <div class="form-group mt-5 ">
                                                                   <p class="h3 ">尺寸表:</p>
                                                                   <label class="h2 "> {{product.content}}</label>
                                                                
                                                            </div> -->
                                                              </div>                                                                    
                          </div>
                          
                                        
                        </div>
                        
                        <div class="col-lg-8 text-center text-lg-left">
                                <h1 class="font-weight-bold">{{product.title}}</h1>        
                                <p class="">{{product.description}}</p>
                                        
                                <img class='w-100 mb-3' :src='product.imageUrl'>
                                <img class='w-100 mb-3' src="https://medias.spotern.com/spots/w640/142230-1532336916.jpg" alt="" >
                                <img class='w-100 mb-3' src="https://medias.spotern.com/spots/w640/130034-1532336916.jpg" alt="">
                        </div>
                </div>
                            </div>
    </div>
</template>


<script>
import HomeNavbar from './HomeNavbar';                       //載入HomeNavbar元件
import Jumbotron from './Jumbotron';                        //載入Jumbotron元件
import Alert from './AlertMessage';  //載入Aleart元件
import bus from '../eventBus'
export default {
    data(){
        return{
            productId:"",
            product:{},
            num:1,
            isLoading:false,
        }
    },
    methods:{
        getProduct(){
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/product/${vm.productId}`;
        this.$http.get(url).then((response) => {
        console.log('getProduct',response.data);
        vm.product = response.data.product
        bus.$emit('Update', this.product);      //畫面更新觸發購物車取得事件
      });
        },
        addCart(){
        const vm = this;
         vm.isLoading = true;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/cart`;
         const cart = {
          product_id:vm.productId,
          qty:vm.num
      }
      this.$http.post(url,{data:cart}).then((response) => {
        console.log('addtoCart',response);
        bus.$emit('carts', vm.product)
        this.$bus.$emit('message:push','已加入購物車','danger') //上傳成功Alert訊息
        vm.isLoading = false;
      });
        },
        home(){
          this.$router.push('/Home/Menu')
        },
    },
    created(){
        this.productId = this.$route.params.id; //取得網址id orderId對應路由名稱
        this.getProduct();
        
    },
    components:{
      Jumbotron,
      HomeNavbar,
      Alert
    }
}
</script>

<style lang="">
  .container{
     font-family: "微軟正黑體"
  }
</style>


