<template>
    <div class="size">
    <nav class="navbar navbar-dark fixed-top bg-white  py-1 shadow ">
     <span class=" mb-0 h4 font-weight-bold text-danger">
        <a class="nav-link text-danger"  href="#" @click.prevent="home"><i class="fab fa-stripe-s  mr-2 text-danger " ></i>六角潮流賣賣</a>
        </span>
        <div>
          <a class="btn btn-outline-danger  btn-sm btn-cart mr-3" href="#" @click="SignIn">
        管理員登入
      </a>
           <ShoppingCart  class="inline"/>
        </div>
</nav>
    </div>
</template>

<script>
import $ from 'jquery';
import ShoppingCart from './ShoppingCart';                //載入ShoppingCart元件
export default {
  methods:{
    SignIn(){
        this.$router.push('/login')                  //$router注入的路由方式
    },
    home(){
      this.$router.push('/Home')                  //$router注入的路由方式
    }
  },
  components:{
    ShoppingCart
  }
}
</script>

<style lang="scss">
@media (min-width: 400px) {
.size{
  margin-bottom:70px; 
}
}
@media (min-width: 576px) {
  .size{
  margin-bottom:0px; 
}
}
nav{
        font-family: "微軟正黑體";
    }


.btn-outline-danger:hover{
  color:brown;
  background-color: #fff;
  border-color: brown;
}
.inline{
  display: inline;
}
</style>


