<template>
    <div class="col-lg-3 ">
    <loading :active.sync="isLoading"></loading>     <!-- loading元件 -->
    <div class="list-group sticky-top mb-4 h5 ">
    <a  class="list-group-item list-group-item-action badge-light color-danger bg-hover border border-danger active" data-toggle="list" href="#t-shirts"  @click.prevent="changeProduct('All')" ><i class="fas fa-tshirt mr-2"></i>All</a>
    <a  class="list-group-item list-group-item-action badge-light color-danger bg-hover border border-danger " data-toggle="list" href="#t-shirts"  @click.prevent="changeProduct('T-shirt')" ><i class="fas fa-tshirt mr-2"></i>T-shirt</a>
    <a  class="list-group-item list-group-item-action badge-light color-danger bg-hover border border-danger" data-toggle="list" href="#hoodie" @click.prevent="changeProduct('LongSleeve')"><i class="fas fa-tshirt mr-2"></i>LongSleeve</a>
     <a  class="list-group-item list-group-item-action badge-light color-danger bg-hover border border-danger" data-toggle="list" href="#TankTop" @click.prevent="changeProduct('TankTop')"><i class="fas fa-tshirt mr-2"></i>TankTop</a>
    <a  class="list-group-item list-group-item-action badge-light color-danger bg-hover border border-danger" data-toggle="list" href="#Sweater" @click.prevent="changeProduct('Sweater')"><i class="fas fa-tshirt mr-2"></i>Sweater</a>
     <a  class="list-group-item list-group-item-action badge-light color-danger bg-hover border border-danger" data-toggle="list" href="#Accessories" @click.prevent="changeProduct('Shoes')"><i class="fas fa-tshirt mr-2"></i>Shoes </a>
    </div>
          <!-- <div class="col-lg-12">
         <HomePagination :pagination="pagination" :filterProducts="filterProducts" @getPageProducts="getProducts"/>
        </div> -->
       

    </div> 
</template>



<script>
import bus from '../eventBus'
export default {
    data(){
        return{
        products:[],
        productsAll:[],
        filterProducts:[],
        pagination:{},
        isLoading:false,
    }
    },
    methods:{
        getProducts(page = 1){
        const api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/products?page=${page}`;       //透過API將後端產品內容，加入 products 內
        const vm =this;
        vm.isLoading =true;                              //畫面loading開啟
        this.$http.get(api).then((response) => {
        console.log('產品列表',response.data)
        vm.products = response.data.products;
        vm.pagination = response.data.pagination        //將目前頁數存入pagination內
        bus.$emit('pagination', vm.pagination)
        bus.$emit('filter', vm.products)
        vm.isLoading =false;                            //畫面loading關閉
        })
        },
        pag(page = 1){
            const api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/products?page=${page}`;       //透過API將後端產品內容，加入 products 內
            const vm =this;
            vm.isLoading =true;                              //畫面loading開啟
            this.$http.get(api).then((response) => {
            vm.pagination = response.data.pagination        //將目前頁數存入pagination內
            vm.products = response.data.products;
            bus.$emit('pagination', vm.pagination)
            bus.$emit('filter', vm.products)
            vm.isLoading =false;                            //畫面loading關閉
        })
        },
        changeProduct(value){
            const vm = this;
            if(value == 'All'){
                vm.filterProducts = vm.products.filter(function(item){
                return item
            })
            this.pag()
            }else{
                vm.filterProducts = vm.productsAll.filter(function(item){
                return item.category == value;
            })
            }
            bus.$emit('filter', vm.filterProducts)
            // bus.$emit('pagination', vm.pagination)
        },
    },
    created() {
        let api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/products/all`; 
        const vm =this;
        this.$http.get(api).then((response) => {
        vm.productsAll = response.data.products;
        bus.$emit('productsAll', vm.productsAll)
        });
        this.getProducts();
        vm.changeProduct('T-shirt')
          bus.$on('getPageProducts', (item) => {
            vm.getProducts(item)
        })
    },
}
</script>




















<style lang="">
.bg-hover:hover{
    background-color: #dc3545 !important;
    color:white !important;
}
.color-danger{
    color: #dc3545 !important;
}
.list-group-item.active{
    z-index: 2 !important;
    color: #fff !important;
    background-color: #dc3545 !important;
    border-color: #dc3545 !important;
}
.color-danger-a{
    color: #dc3545 !important;
    text-decoration: none !important;
}

.color-white-a{
    color: #fff !important;
    text-decoration: none !important;
}
</style>


