<template>
    <div>
         <nav class="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow ">
  <a class="navbar-brand col-sm-3 col-md-2 mr-0" href="#">後台系統</a>
  <ul class="navbar-nav px-3 ">
     <li class="nav-item text-nowrap d-md-none">
      <a class="nav-link" href="#" @click.prevent="Product">產品列表</a>
    </li>
    <li class="nav-item text-nowrap d-md-none">
      <a class="nav-link" href="#" @click.prevent="Order">訂單列表</a>
    </li>
    <li class="nav-item text-nowrap d-md-none">
      <a class="nav-link" href="#" @click.prevent="Voucher">優惠卷</a>
    </li>
    <li class="nav-item text-nowrap">
      <a class="nav-link" href="#" @click.prevent="sigout">登出</a>
    </li>
  </ul>
</nav>
    </div>
</template>

<script>
export default {
  name:'Navbar',
  methods: {
    sigout(){
      const vm = this;
      const url = `${process.env.APIPATH}/logout`;
      this.$http.post(url).then((response)=>{
        if(response.data.success){
          vm.$router.push('/signin')
        }
      })
    },
    Product(){
      this.$router.push('/admin/Products')
    },
    Order(){
      this.$router.push('/admin/Order')
    },
    Voucher(){
      this.$router.push('/admin/Voucher')
    }
  },
}
</script>







