<template>
    
    <div class="col-lg-12">
     <loading :active.sync="isLoading"></loading>     <!-- loading元件 -->
    <div class="tab-content">
    <div class="tab-pane active" id="t-shirts" role="tabpanel">
    <div class="row">  <!-- //增加格線系統，讓卡片元素可以自由排列 -->                         
                            <div class="col-lg-4 mb-4" v-for="item in filterProducts" :key='item.id' >
                            <div class="card text-center h-100 box-shadow border-0" >
                             <a  @click.prevent="productPage(item.id)">
                             <div style="height: 300px; background-size: contain; background-position: center; background-repeat:no-repeat;" :style="{backgroundImage: `url(${item.imageUrl})`}"></div>
                             </a>
                             <div class="card-body">
                             <h5 class="card-title font-weight-bold">{{item.title}}</h5>
                             <!-- <p class="card-text">{{item.description}}</p> -->
                             </div>
                             <div class="card-footer bg-white border-top-0">
                             <p class="font-weight-bold h5">售價  {{item.price | currency }}</p>
                             <div class="btn-group" role="group" aria-label="Basic example"> 
                            <button type="button" class="btn btn-danger" @click="addtoCart(item.id)">加入購物車</button>
             </div>
             </div>
             </div>  
             </div>

                             <!-- <div class="col-lg-4 mb-4" v-for="item in product" :key='item.id'>
                            <div class="card text-center h-100 box-shadow border-0" >
                            <a href="index2.html" >
                             <div style="height: 300px; background-size: contain; background-position: center; background-repeat:no-repeat;" :style="{backgroundImage: `url(${item.imageUrl})`}"></div>
                             </a>
                             <div class="card-body">
                             <h5 class="card-title font-weight-bold">{{item.title}}</h5>
                             <p class="card-text">{{item.description}}</p>
                             </div>
                             <div class="card-footer bg-white border-top-0">
                             <p class="font-weight-bold h5">售價  {{item.price}}</p>
                             <div class="btn-group" role="group" aria-label="Basic example">
                                 
                                <button type="button" class="btn btn-danger">加入購物車</button>
             </div>
             </div>
             </div>  
             </div> -->
               
     </div>                                                              
    </div>
    </div>
    </div>
</template>

<script>
import bus from '../eventBus'
export default {
    data(){
        return{
        Search:"",
        is_search:false,
        isLoading:false,
        product:{},
        carts:[],
        filterProducts:[]
        }
    },
    methods:{
        addtoCart(id,qty = 1){
        const vm = this;
         vm.isLoading = true;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/cart`;
         const cart = {
          product_id:id,
          qty
      }
      this.$http.post(url,{data:cart}).then((response) => {
        console.log('addtoCart',response);

        vm.getShoppingCart();
        this.$bus.$emit('message:push','已加入購物車','danger') //上傳成功Alert訊息
        vm.isLoading = false;
      });
            },
        getShoppingCart(){
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/cart`;
        this.$http.get(url).then((response) => {
        console.log('getCart',response.data);
        vm.carts = response.data.data
        bus.$emit('cart', vm.carts)
      });
        },
        productPage(id){
          this.$router.push(`/ProductsPage/${id}`)
          console.log(123)
        },
        },
    created(){
        this.getShoppingCart()
        bus.$on('filter', (item) => {
            this.filterProducts = item
     })
        bus.$on('filterProducts', (item) => {
            this.filterProducts = item
     })
    },
}
</script>


<style lang="">
.box-shadow{
    box-shadow: 0 3px 5px rgba(0,0,0,.16);
    transition:box-shadow .3s;
    /* 邊框陰影+延遲效果 */
}
.box-shadow:hover{
    box-shadow: 0 4px 10px rgba(0,0,0,.24)
    /* //滑動時加粗效果 */
}
.tab-content{
    font-family: "微軟正黑體"
}
</style>


