<template>
         <nav class="col-md-2 d-none d-md-block bg-light sidebar">
      <div class="sidebar-sticky">
        <h6 class="sidebar-heading d-flex justify-content-between align-items-center px-3 mt-4 mb-1 text-muted">
          <span>管理員</span>
          <a class="d-flex align-items-center text-muted" href="#">
            <span data-feather="plus-circle"></span>
          </a>
        </h6>
        <ul class="nav flex-column">
          <li class="nav-item">
            <router-link to="/admin/Products" class="nav-link font-weight-bold" @click.native="ProductPage" :class="{'active':Product}"><i class="fas fa-box-open pr-2"></i>產品列表</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/admin/Order" class="nav-link font-weight-bold" @click.native="OrderPage" :class="{'active':Order}"><i class="fas fa-money-check pr-2"></i>訂單列表</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/admin/Voucher" class="nav-link font-weight-bold" @click.native="VoucherPage" :class="{'active':Voucher}"><i class="fas fa-money-check-alt pr-2"></i>優惠卷</router-link>
          </li>
        </ul>
      </div>
    </nav>
</template>



<script>
import $ from "jquery";
export default {
  data:function(){
    return{
      Product:true,
      Order:false,
      Voucher:false,
      Simulation:false,
      Shopping:false,
      Checkout:false,
    }
  },
  methods:{
    ProductPage(){
      this.Product = true;
      this.Order =false;
      this.Voucher =false;
      this.Simulation=false;
      this.Shopping=false;
      this.Checkout=false;
    },
    OrderPage(){
      this.Product =false;
      this.Order =true;
      this.Voucher =false;
      this.Simulation=false;
      this.Shopping=false;
      this.Checkout=false;
    },
    VoucherPage(){
      this.Product = false;
      this.Order =false;
      this.Voucher =true;
      this.Simulation=false;
      this.Shopping=false;
      this.Checkout=false;
    },
    SimulationPage(){
      this.Product = false;
      this.Order =false;
      this.Voucher =false;
      this.Simulation = true;
      this.Shopping=false;
      this.Checkout=false;
    },
    ShoppingPage(){
      this.Product = false;
      this.Order =false;
      this.Voucher =false;
      this.Simulation = false;
      this.Shopping=true;
      this.Checkout=false;
    },
    CheckoutPage(){
        this.Product = false;
      this.Order =false;
      this.Voucher =false;
      this.Simulation = false;
      this.Shopping=false;
      this.Checkout=true;
    }
  }
}
</script>



<style scoped>

nav {
  font-family: "微軟正黑體";
}

</style>


