// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

//vue的進入點綁定主要的index.html
//第三放套件往上放
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import Loading from 'vue-loading-overlay';          //Loading效果，是一個元件必須啟用它
import 'vue-loading-overlay/dist/vue-loading.css';  //Loading效果樣式
import 'bootstrap';              //如果有使用bootstrap的jquery功能就必須import
import "jquery";                 //有使用就必須import jquery
import VeeValidate from 'vee-validate';
//自己套件往下放
import App from './App'  ;
import router from './router';
import './bus' ;                     //彈跳元件
import currencyFilter from './Filter/currency'; //金錢千分位
import date from './Filter/date';
import VueI18n from 'vue-i18n';
import zhTW from 'vee-validate/dist/locale/zh_TW';
import {Validator} from 'vee-validate';




        //自己的套件往下放
Vue.component('Loading',Loading)         //全域方式啟用component
Vue.use(VueAxios, axios)                //啟用axios
Vue.filter('currency',currencyFilter)  //全域方式啟用filter
Vue.filter('date',date)               //全域方式啟用日期格式
Vue.use(VeeValidate);
Vue.use(VueI18n);                      //全域方式啟用驗證
Vue.use({Validator})
VeeValidate.Validator.localize('zh_TW',zhTW) //Email中文化
Vue.config.productionTip = false   //cookies
axios.defaults.withCredentials = true




const i18n = new VueI18n({
  locale: 'zhTW'
});
Vue.use(VeeValidate, {
  i18n,
  dictionary: {
    zhTW
  },
  events: 'input',
});








new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,              //增加入路由配置
  i18n,
});


//導航守衛
router.beforeEach((to,from,next)=>{
  if(to.meta.requiresAuth){
    const api =`${process.env.APIPATH}/api/user/check`;
    axios.post(api).then((response) => {
   console.log(response.data)
   if(response.data.success){
    next();
   }else{
     alert('請先登入')
    next({
      path:'/login'
    });
   }
   })
  }else{
    next();
  }
})




