
<template>
     <div>
         <loading :active.sync="isLoading" ></loading>     <!-- loading元件 -->
    <div class="text-right mt-4">
      <button class="btn btn-dark" @click='openModal(true)'>建立新的產品</button>      <!-- openModal(true) = isNew = true -->
    </div>
    <table class="table mt-4 table-dark table-hover ">
      <thead>
        <tr>
          <th width="120">分類</th>
          <th>產品名稱</th>
          <th width="120">原價</th>
          <th width="120">售價</th>
          <th width="100">是否啟用</th>
          <th width="80">編輯</th>
          <th width="80">刪除</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">
            {{ item.origin_price | currency}}
          </td>
          <td class="text-right">
            {{ item.price | currency}}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <button class="btn btn-light btn-sm" @click="openModal(false,item)">編輯</button>   <!-- openModal(false,item) = isNew = false -->
          </td>
          <td>
            <button class="btn btn-danger btn-sm" @click="opendelProductModal(item)">刪除</button>   
          </td>
        </tr>
      </tbody>
    </table>

 <!-- pagination -->
<Pagination :pagination="pagination"  @getPageProducts="getProducts"/>   <!-- 綁定pagination內容，且元件內getPageProducts觸發getProducts -->
<!-- Modal -->
<Modal :tempProduct='tempProduct'  @getupdateProducts="updateProduct" ref='refs' />
<!-- RemoveModal -->
<RemoveModal :tempProduct='tempProduct' @getRemoveDatas="removeData"/>
  </div>
</template>


<script>
/* global $ */    //如果有使用ESLint加這段才部會跳錯，但是還是必須import $ from "jquery"
import $ from "jquery";
import Pagination from '../Pagination';
import RemoveModal from '../RemoveModal';
import Modal from '../Modal';

export default {
    data(){
        return{
            products:[],                         //將遠端資料存入
            pagination:{},                       //定義分頁
            tempProduct:{},                    //將tempProduct宣告成物件
            isNew:Boolean,                    //判別資料是新的還舊的
            isLoading:false,                 //控制畫面loading
        };
    },
    methods:{
        getProducts(page = 1){     //使用ES6預設參數，假設沒有代參數就將1代入
        const api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/products?page=${page}`;       //透過API將後端產品內容，加入 products 內
        const vm =this;
        vm.isLoading =true;                              //畫面loading開啟
        this.$http.get(api).then((response) => {
        console.log('getProducts',response.data)
        vm.products = response.data.products;
        vm.isLoading =false;                            //畫面loading關閉
        vm.pagination = response.data.pagination        //將目前頁數存入pagination內
})
       },
       openModal(isNew,item){
           let vm = this;
           if(isNew){                                         //如果isNew是true代表是新資料給予空的Modal面板
               this.tempProduct ={};                         //將tempProduct物件清空
               this.$refs.refs.$refs.files.value = ''                    //將input的value清空
              //  console.log(this.$refs.refs.$refs.files.value)

               this.isNew = true;
           }else{                                             //如果isNew是false代表是舊資料給予原有Modal面板
               this.tempProduct = Object.assign({},item);     //避免物件傳參考特性使用Object.assign({},item)
               this.isNew = false;
           }
           $('#productModal').modal('show');     //透過vue的methods去觸發新增產品，避免資料尚未載入就用bootstrap的jquery觸發

       },
       updateProduct(){
        let api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/product`;    //透過API將新增的產品加入 products 內
        let httpMethod = 'post';
        const vm =this;
        if(!vm.isNew){                                                                                         //如果是新增物件此時的isNew是true不會執行這段，但是如果是修改後isNew是false就會執行這段
           api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/product/${vm.tempProduct.id}`;    //透過API將修改後產品存入後端
           httpMethod = 'put';                                                                                //改變傳遞方式，因為修改是"put"
        }      
        this.$http[httpMethod](api,{data:vm.tempProduct}).then((response) => {                    //因為格式是{data:{}} ，動態改變傳遞方式[httpMethod]
        console.log('updateProduct',response.data)
        if(response.data.success){                      //儲存成功時
            $('#productModal').modal('hide');           //將modal關閉
            vm.getProducts();                           //畫面重新更新
        }else{
            $('#productModal').modal('hide');
            vm.getProducts();
            alert('新增失敗')
        }
})
       },
        opendelProductModal(item){
          $('#delProductModal').modal('show');                      //開啟刪除model
          this.tempProduct = Object.assign({},item);                //將item的值賦予到tempProduct內 Object.assign({},item)避免傳參考

       },
       removeData(tempProduct){                                     //直接將model的tempProduct代入
          const vm =this;
          const api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/product/${vm.tempProduct.id}`;     //透過API將產品刪除
          this.$http.delete(api).then((response) => {
          console.log('removeData',response.data)
          vm.getProducts();                           //畫面重新更新
         })
         $('#delProductModal').modal('hide');         //將modal關閉     
       },
    },
    created(){
         this.getProducts()                    //生命週期created()觸發getProducts()撈取資料
    },
    components:{
        Pagination,
        RemoveModal,
        Modal,
    }
}
</script>




