<template>
    <div>
         <loading :active.sync="isLoading" ></loading>     <!-- loading元件 -->
        <div class="my-5 row justify-content-center">
  <form class="col-md-6" @submit.prevent="payOrder">
    <table class="table">
      <thead>
        <th>品名</th>
        <th></th>
        <th>數量</th>
        <th class="text-right">單價</th>
      </thead>
      <tbody>
        <tr v-for="item in order.products" :key="item.id">
          <td class="align-middle">{{ item.product.title }}</td>
          <td><img :src=item.product.imageUrl style="height:30px;"></td>
          <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
          <td class="align-middle text-right">{{ item.final_total | currency}}</td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td></td>
          <td colspan="2" class="text-right">總計</td>
          <td class="text-right">{{ order.total | currency}}</td>
        </tr>
      </tfoot>
    </table>

    <table class="table">
      <tbody>
        <tr>
          <th width="100">Email</th>
          <td>{{ order.user.email }}</td>
        </tr>
        <tr>
          <th>姓名</th>
          <td>{{ order.user.name }}</td>
        </tr>
        <tr>
          <th>收件人電話</th>
          <td>{{ order.user.tel }}</td>
        </tr>
        <tr>
          <th>收件人地址</th>
          <td>{{ order.user.address }}</td>
        </tr>
        <tr>
          <th>付款狀態</th>
          <td>
            <span v-if="!order.is_paid">尚未付款</span>
            <span v-else class="text-success">付款完成</span>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="text-right" v-if="order.is_paid === false">
      <button class="btn btn-danger">確認付款去</button>
    </div>
  </form>
  
</div>
    </div>
</template>

<script>

export default {
    data(){
        return{
            orderId:{},
            order:{
                user:{}     //避免Email多層錯誤
            },
            isLoading:false
        }
    },
    methods:{
        getOrder(){
     const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/order/${vm.orderId}`;
      this.$http.get(url).then((response) => {
        console.log('getOrder',response.data);
        vm.order = response.data.order;
      });
        },
        payOrder(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/pay/${vm.orderId}`;
            vm.isLoading = true;
            this.$http.post(url).then((response) => {
            console.log('payOrder',response.data);
            if(response.data.success){
                vm.getOrder()
                this.$emit('changeCheck'); //內部元件觸發
                vm.isLoading = false;
                vm.$router.push(`/Checkouts/CarryOutOrder`)

            }

        })
        }
    },
    created(){
        this.orderId = this.$route.params.orderId; //取得網址id orderId對應路由名稱
        this.getOrder();
    }
}
</script>


