<template>
    <div>
        <div class="modal fade" id="orderModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">訂單內容</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <table class="table " >
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th class="text-right">單價</th>
          </thead>
          <tbody>
            <tr v-for="item in products" :key="item.id" >
              <td class="align-middle">
                <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title}}
                <div class="text-success" v-if="item.coupon">
                  已套用優惠券
                </div>
              </td>
              <!-- <td class="align-middle">{{ item.qty}}</td> -->
              
              <td>
                <select name="" class="form-control mt-3" v-model="item.qty">
              <option :value="num" v-for="num in 10" :key="num">
                選購 {{num}} {{item.product.unit}}
              </option>
            </select>
            </td>
              <td class="align-middle text-right">{{ item.product.price * item.qty}}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{}}</td>
            </tr>
            <!-- <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}</td>
            </tr> -->
          </tfoot>
        </table>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary" @click="add">Save changes</button>
      </div>
    </div>
  </div>
</div>
    </div>
</template>

<script>
export default {
    props:['sortOrderitem','sortOrderproducts','products'],
    data:function(){
        return{
            data: {
    "create_at": "",
    "is_paid": false,
    "message": "",
    "payment_method": "",
    // "products": {
    //      "num": "",
    // },
    "total": "",
    "user": {}
  }
        }
    },
    methods:{
        add(){

        }
    },
    created(){
     
    }
}
</script>

products:{
    -LkdrTUoKpma7dLB7RHa:{
        final_total:3000
        id:"-LkdrTUoKpma7dLB7RHa"
        product:{}
        product_id:"-LkEJwqtGY-NEPp31waI"
        qty:3
        total:3000
    }

}



