<template>
    <div>
<loading :active.sync="isLoading" ></loading>     <!-- loading元件 -->
<div class="modal fade" id="VoucherModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLongTitle" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLongTitle">新增優惠卷</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <div class="row">
            <div class="col-sm">
                   <div class="form-group">
              <label for="title">輸入優惠卷名稱</label>
              <input type="text" class="form-control" id="title" v-model="data.title"
                placeholder="請輸入優惠卷名稱" >
            </div>
                <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"  :true-value='1' :false-value='0' v-model="data.is_enabled"
                  id="is_enabled"  >
                <label class="form-check-label" for="is_enabled">
                  是否啟用
                </label>
              </div>
            </div>
          <div class="form-group">
              <label for="title">輸入優惠比率</label>
              <input type="number" class="form-control" id="title" v-model="data.percent"
                placeholder="請輸入優惠比率" >
            </div>
              <div class="form-group">
              <label for="due_date">到期日</label>
              <input type="date" class="form-control" id="due_date"
                v-model="due_date">
            </div>
          <div class="form-group">
              <label for="title">輸入優惠代碼</label>
              <input type="text" class="form-control" id="title" v-model="data.code"
                placeholder="請輸入優惠代碼" >
            </div>
            </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
        <button type="button" class="btn btn-primary" @click="addVoucher">儲存</button>
      </div>
    </div>
  </div>
</div>
    </div>
</template>

<script>
import $ from "jquery";
export default {
    data(){
        return{
            isLoading:false, 
            due_date:new Date(),
        }
    },
    props:['data','isNew'],
    methods:{
        addVoucher(){
        let api =`${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/coupon`
        let httpMethod = 'post';
       const vm = this;
       if(!vm.isNew){
           api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPTH}/admin/coupon/${vm.data.id}`;
           httpMethod = 'put';
       }
        vm.isLoading =true;
      const Voucher ={
          title:vm.data.title,
          is_enabled:vm.data.is_enabled,
          percent:vm.data.percent,
          due_date:vm.data.due_date,
          code:vm.data.code
      }
      this.$http[httpMethod](api,{data:Voucher}).then((response) => {
        console.log('addVoucher',response);
        vm.isLoading = false;
        $('#VoucherModal').modal('hide');                      //開啟刪除model
        this.$emit('addVoucher')
        })
        }
    },
        watch:{
        due_date(){
        const vm = this;
        const timestamp = Math.floor(new Date(vm.due_date) / 1000);
        vm.data.due_date = timestamp;
        console.log(vm.data.due_date)
        }
    },
}
</script>


