import Vue from 'vue';

Vue.prototype.$bus = new Vue();

// Vue.prototype.$bus = new Vue();
// $bus的用途：在Vue本體下註冊這個變數，使得所有組件都能夠取用，
// 且這個變數是客製化的，要改成 Vue.prototype.apple也可以，
// $的功能是要提醒使用者：它是一個全域變數。


// - Mixins 是預先定義部分功能的概念
// 再透過元件的引入
// 因此，實際運行時還是運用該元件的資料
// (雖然可以預先定義，但混合後還是被視為該元件)
// 所以並不適合作為跨組件通訊

// - Event Bus 直接透過 emit 做簡易的跨組件通訊 (元件對元件)
// 而 Vuex 則是做整體的資料流管理 (元件對整個資料狀態)
// 因此 Event Bus 通常會運用在比較小的專案
// 在數量多的時候會較難追蹤有哪些元件使用 Event Bus
// Vuex 則是將所有資料取出統一管理


