<template>
    <div>
    <nav aria-label="Page navigation example ">
  <ul class="pagination justify-content-center fixed-bottom">
    <li class="page-item" :class="{'disabled':!pagination.has_pre}"  > <!-- pagination.current_page 目前頁數 -->
      <a class="page-link text-dark" href="#" aria-label="Previous" @click.prevent='getProducts(pagination.current_page - 1)'>
        <span aria-hidden="true">&laquo;</span>
      </a>
    </li>
    <li class="page-item" v-for="page in pagination.total_pages" :key='page' :class="{'active':pagination.current_page === page}" @click.prevent='getProducts(page)'><a class="page-link" href="#" :class="{'text-dark':pagination.current_page !== page}" >{{ page }}</a></li>
    
    <li class="page-item" :class="{'disabled':!pagination.has_next}"  >
      <a class="page-link text-dark" href="#" aria-label="Next" @click.prevent='getProducts(pagination.current_page + 1)'>
        <span aria-hidden="true">&raquo;</span>
      </a>
    </li>
  </ul>
</nav>

    </div>
</template>

<script>
export default {
    props:['pagination'],                           //接收外層pagination內容
    methods: {
        getProducts(page){                         //將pagination.current_page 目前頁數代入
            this.$emit('getPageProducts',page)     //內部元件觸發，將外層props內容emit回去
        }
    },
}
</script>



